import './style.css';

import React, { useState } from 'react';

import Cart from '../../components/Cart';
import Menu from '../../components/Menu';
import _ from 'lodash';

function Home() {
  const [cartList, setCartList] = useState(
    localStorage.getItem('Cart') !== null ? JSON.parse(localStorage.getItem('Cart')) : []
  );
  let data = [
    {
      menuName: 'Dinner',
      menuItems: [
        {
          itemId: 1,
          itemPrice: '12',
          itemName: 'Lasagne',
          itemDescription:
            'Meat and cheese layered between house-made pasta with bell peppers and onions.',
        },
        {
          itemId: 2,
          itemPrice: '10',
          itemName: 'Cheese Ravioli',
          itemDescription: 'Cheese-filled ravioli served with house red sauce.',
        },
        {
          itemId: 3,
          itemPrice: '14',
          itemName: 'Chicken Parmesan',
          itemDescription:
            'Breaded chicken topped with marinara sauce and lots of cheese served over house made spaghetti.',
        },
      ],
    },
    {
      menuName: 'Dessert',
      menuItems: [
        {
          itemId: 45,
          itemPrice: '10',
          itemName: 'Chocolate Lava Cake',
          itemDescription: 'Dark chocolate molten lava cake. Serves 2-3.',
        },
        {
          itemId: 47,
          itemPrice: '8',
          itemName: 'Tiramisu',
          itemDescription:
            'Layers of espresso-soaked ladyfingers and marscarpone cream topped with cocoa and espresso powder.',
        },
        {
          itemId: 49,
          itemPrice: '5',
          itemName: 'Cannolis',
          itemDescription:
            'Three flaky pastries enclosing a rich, ricotta filling dotted with decadent chocolate chips.',
        },
        {
          itemId: 50,
          itemPrice: '5',
          itemName: 'Cappuccino',
          itemDescription: 'Steamed milk with two ristretto shots of espresso.',
        },
      ],
    },
  ];

  const updateCartList = (name, price, id) => {
    const exists = _.findIndex(cartList, { name: name }) !== -1 ? true : false;
    if (exists) {
      const index = _.findIndex(cartList, { name: name });
      setCartList(
        cartList.map((item) => {
          if (item === cartList[index]) return { ...item };
          return item;
        })
      );
    } else {
      setCartList((prevCartList) => [
        ...prevCartList,
        {
          name: name,
          price: price,
          id: id,
        },
      ]);
    }
  };

  const addCart = (name, price, id) => {
    updateCartList(name, price, id);
  };

  const removeCart = (id) => {
    const newCartList = cartList.filter((cartItem) => {
      return cartItem.id !== id;
    });
    setCartList(newCartList);
  };

  return (
    <div className="home">
      <Menu data={data} addCart={addCart}></Menu>
      <Cart cart={cartList} removeCart={removeCart} />
    </div>
  );
}
export default Home;
