import './style.css';

import React from 'react';

export default class MenuItem extends React.Component {
  render() {
    return (
      <div className="menu-item" id={`menu-item-${this.props.itemId}`}>
        <div className="item-info">
          <p>
            <b>name:</b> {this.props.itemName}
          </p>
          <p>
            <b>description:</b> {this.props.itemDescription}
          </p>
          <p>
            <b>price:</b> ${this.props.itemPrice}
          </p>
        </div>
        <button
          className="addCartButton"
          onClick={() => {
            this.props.addCart(this.props.itemName, this.props.itemPrice, this.props.itemId);
          }}
        >
          Add to Cart
        </button>
      </div>
    );
  }
}
